@import "../../index.scss";
@import "../../styles.scss";

.StatsBlockContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 3px;

  &.mini {
    position: fixed;
    top: calc(100% - 100px);
    margin-top: 0;
  }
}

.StatsBlock {
  width: 80%;
  background-color: $main;
  padding: 0 25px;
  border-radius: 10px;
  height: 0;
  margin-bottom: 15px;
  display: grid;
  grid-template-columns: repeat(11, 1fr);
  transition: height 0.2s ease-in-out;
  gap: 20px;

  &.game {
    height: 70px;
  }

  &.mini {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 5px;
  }
}

.TextStatsBlock {
  opacity: 0;
  align-self: center;
  font-size: 20px;
  font-weight: $medium;
  transition: opacity 0.2s ease-in-out;
  font-family: $appfont;

  &.game {
    opacity: 1;
  }
}

.IconContainer {
  margin-top: 5px;
  align-self: center;
}

.Icon {
  align-self: center;
  width: 50px;
  height: 50px;
}

.NumberStats {
  align-self: center;
  font-size: 30px;
  font-weight: $medium;
  transition: opacity 0.2s ease-in-out;

  &.game {
    opacity: 1;
  }

  &.correct {
    color: rgb(28 90 28 / 80.1%);
  }

  &.wrong {
    color: rgb(136 42 18 / 80.1%);
  }
}

@media (max-width: $wide-screen2) {
  .StatsBlockContainer {
    top: calc(100% - 90px);
  }

  .StatsBlock {
    background-color: rgb(219 219 219 / 71.2%);
  }

  .Icon {
    width: 45px;
    height: 45px;
  }
}

@media (max-width: $medium-screen2) {
  .Icon {
    width: 40px;
    height: 40px;
  }
}

@media (max-width: $medium-screen1) {
  .Icon {
    width: 35px;
    height: 35px;
  }
}
