@import '../../index.scss';
@import '../../styles.scss';

.CardHolder {
  margin: 25px 0;
  padding: 25px;
  width: 80%;
  // height: 80%;
  // min-height: 80%;
  background-color: $main;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 20px;
  border-radius: 10px;

  &.word {
    grid-template-columns: repeat(4, 1fr);
  }

  &.offline {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(1, 1fr);
  }
}

.offlineContentReloadButton {
  background-color: rgb(128 128 128 / 45.9%);
  cursor: pointer;
  color: black;
  height: 25px;
  width: 90px;
  box-shadow: $box-shadow-small;
  border: none;
  border-radius: 10px;

  &:hover {
    background-color: rgb(128 128 128 / 54.8%);
  }

  &:active {
    background-color: $secondary;
  }
}

.Link {
  text-decoration: none;
  font-size: 40px;
  color: black;
  z-index: 11;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.Card {
  width: 100%;
  height: 250px;
  background-color: white;
  align-self: center;
  justify-self: center;
  border-radius: 10px;
  position: relative;
  transition: all 0.1s linear;
  box-shadow: $box-shadow-small;
  z-index: 10;
  font-size: 40px;

  &:hover {
    transform: scale($scale-value);
    box-shadow: $box-shadow-big;
  }
}

@media (max-width: $wide-screen2) {
  .CardHolder.word {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: $wide-screen1) {
  .Card {
    font-size: 32px;
  }
}

@media (max-width: $medium-screen2) {
  .CardHolder {
    grid-template-columns: repeat(2, 1fr);
  }

  .CardHolder.word {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: $mobile-screen3) {
  .CardHolder {
    grid-template-columns: repeat(1, 1fr);
  }

  .CardHolder.word {
    grid-template-columns: repeat(1, 1fr);
  }
}

@media (max-width: $minimum-screen) {
  .CardHolder {
    width: 280px;
  }
}
